<template>
  <q-layout view="LHh lpR lFf">
    <DrawerMain />

    <q-page-container class="bg-color">
      <router-view v-slot="{ Component }">
        <q-pull-to-refresh
          @refresh="refresh"
          color="secondary"
          icon="icon-refresh-cw-01"
          bg-color="translucent-light shadows-lg"
        >
          <component :is="Component" />
        </q-pull-to-refresh>

        <!-- Dev Tools -->
        <q-page-sticky
          v-if="$DEV_MODE"
          style="z-index: 10"
          :offset="[10, 25]"
          position="bottom-right"
        >
          <q-fab
            flat
            unelevated
            direction="up"
            v-model="devTools"
            padding="sm"
            color="primary"
            icon="icon-growmodo"
            vertical-actions-align="right"
            v-ripple="{ color: 'accent' }"
            class="hover-bg-accent"
          >
            <q-fab-action
              v-if="$DEV"
              label-position="left"
              color="primary"
              @click="sendTestNotif"
              label="Test Notif"
              class="rounded"
            />
            <q-fab-action
              v-if="$DEV"
              label-position="left"
              color="primary"
              @click="$router.push('/404')"
              label="404 Page"
              class="rounded"
            />
            <q-fab-action
              label-position="left"
              color="primary"
              @click="userNotOnBoard = true"
              label="Onboarding"
              class="rounded"
            />
          </q-fab>
          <tooltip-pro
            v-if="!devTools"
            anchor="center left"
            self="center right"
          >
            Test Views
          </tooltip-pro>
        </q-page-sticky>

        <q-page-sticky
          position="bottom-left"
          v-if="$q.screen.width <= 600"
          :offset="[10, 25]"
          style="z-index: 9"
        >
          <q-btn
            flat
            round
            size="sm"
            icon="icon-grid-01"
            :ripple="{ color: 'accent' }"
            class="bg-primary text-white hover-bg-secondary q-pa-sm"
            @click="$root.toggleMainDrawer()"
          />
          <tooltip-pro
            :delay="150"
            :offset="[10, 10]"
            self="center right"
            anchor="center left"
          >
            {{ $root.mainDrawer ? 'Hide' : 'Show' }} Menu
          </tooltip-pro>
        </q-page-sticky>
      </router-view>
    </q-page-container>

    <!-- Onboarding -->
    <q-dialog
      :persistent="startOnBoarding"
      :maximized="startOnBoarding"
      v-model="userNotOnBoard"
    >
      <OnBoarding
        noclose
        @start="initializeOnBoarding"
        class="rounded"
        :style="startOnBoarding ? '' : 'width: 980px; max-width: 80vw'"
        @exit="
          () => {
            userNotOnBoard = false;
            startOnBoarding = false;
          }
        "
      />
    </q-dialog>

    <!-- Dialog Request to Upgrade Plan -->
    <q-dialog v-model="$root.dialogRequestUpgrade">
      <dynamic-dialog-confirmation
        width="375px"
        v-bind="$root.messagesDialogs?.requestUpgrade"
      />
    </q-dialog>

    <!-- Plan Upgrade -->
    <q-dialog
      v-model="$root.upgradePlan"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <Dialog_UpgradeYourPlan
        noclose
        style="width: 700px"
        :style="
          $q.screen.xs && $q.screen.lt.sm
            ? 'max-width: 95vw;'
            : 'max-width: 80vw'
        "
        @requestChanges="$root.upgradePlan = false"
      />
    </q-dialog>

    <!-- Redirect Confirmation -->
    <q-dialog
      v-model="$root.redirect.confirmation"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <RedirectConfirmation
        v-bind="{ options: $root.redirect }"
        style="width: 350px"
      />
    </q-dialog>
  </q-layout>
</template>

<script>
import { defineComponent, defineAsyncComponent } from 'vue';
import { LoadingBar } from 'quasar';
import { displayNotification } from 'src/assets/scripts/notification';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useApp from 'stores/app';

export default defineComponent({
  name: 'MainLayout',

  components: {
    DrawerMain: defineAsyncComponent(() =>
      import('src/components/Drawer_Main.vue')
    ),
    RedirectConfirmation: defineAsyncComponent(() =>
      import('src/components/Dialogs/RedirectConfirmation.vue')
    ),
    Dialog_UpgradeYourPlan: defineAsyncComponent(() =>
      import('src/components/Dialogs/UpgradeYourPlan.vue')
    ),
    OnBoarding: defineAsyncComponent(() =>
      import('src/components/Dialogs/OnBoarding.vue')
    ),
  },

  data() {
    return {
      devTools: false,
      userNotOnBoard: false,
      startOnBoarding: false,
    };
  },

  computed: {
    ...mapState(useUser, ['user', 'activeOrgID']),

    clientOnboarded() {
      return this.user?.onboarded ? true : false;
    },
  },

  async created() {
    this.$router.beforeEach((to, from, next) => {
      next();
      LoadingBar.start();
    });

    this.$router.afterEach((to, from, failure) => {
      setTimeout(() => {
        LoadingBar.stop();
      }, 500);
    });

    // Check if user is onboarded
    setTimeout(() => {
      this.userNotOnBoard =
        this.isOrgOwner() && !this.clientOnboarded ? true : false;
    }, 1500);
  },

  async mounted() {
    await this.init_LoggedIn();
  },

  methods: {
    ...mapActions(useApp, ['init_LoggedIn']),
    ...mapActions(useUser, ['updateAccount', 'isOrgOwner']),

    onBoarded() {
      this.updateAccount({ onBoard: true });
    },

    initializeOnBoarding() {
      this.startOnBoarding = true;
    },

    refresh() {
      setTimeout(() => {
        window.location.href = window.location.href;
      }, 1000);
    },

    sendTestNotif() {
      displayNotification('Test Notification', {
        body: 'Notif click event will refresh the page.',
      }).then((notif) => {
        notif.onclick = () => {
          window.location.href = window.location.href;
        };
      });
    },
  },
});
</script>
